import moviesdb from "../api/moviedb";

export const trending = async () => {
  let res = await moviesdb.get("/trending/all/week", {
    params: { api_key: "c625140e19067452daf7cd175f3fe6be" },
  });

  let trendingList = res.data.results.slice(0, 5);
  return trendingList;
};

