import React, { useState } from "react";
import { HashRouter as Router, NavLink } from "react-router-dom";

import "./Navigation.css";
import logo from "../assets/images/logo.svg";

import NavigationSettings from "./NavigationSettings";
import SearchBox from "./SearchBox";

const Navigation = ({ nav }) => {
  const [collapsed, setCollapsed] = useState(true);
  let smallScreen = window.matchMedia("(max-width: 991.98px)");

  return (
    <nav
      className={`navigation${nav ? " navigation--scrolled" : ""}${
        collapsed ? " navigation--hidden" : ""
      }`}
    >
      <div className="navigation__menu">
        <Router>
          <div
            className={`navigation__brand${
              !collapsed ? " navigation__brand--solid" : ""
            }`}
          >
            <div className="navigation__collapse">
              <button
                className="navigation__hamburger navigation__hamburger--js"
                onClick={() => setCollapsed(!collapsed)}
              >
                <i className="fas fa-bars" />
              </button>
            </div>
            <div className="navigation__logo">
              <NavLink
                exact
                to="/"
                className="navigation__link"
                onClick={() =>
                  smallScreen.matches && !collapsed
                    ? setCollapsed(!collapsed)
                    : null
                }
              >
                <img src={logo} alt="logo" />
              </NavLink>
            </div>
            <SearchBox small />
          </div>
          <div
            className={`navigation__menus${
              collapsed ? " navigation__menus--hidden" : ""
            }`}
          >
            <div className="navigation__menu__left">
              <button
                className="navigation__item"
                onClick={() =>
                  smallScreen.matches ? setCollapsed(!collapsed) : null
                }
              >
                <NavLink
                  exact
                  to="/"
                  className="navigation__link"
                  activeClassName="navigation__link--active"
                >
                  Inicio
                </NavLink>
              </button>
              <button
                className="navigation__item"
                onClick={() =>
                  smallScreen.matches ? setCollapsed(!collapsed) : null
                }
              >
                <NavLink
                  to="/tv-shows"
                  className="navigation__link"
                  activeClassName="navigation__link--active"
                >
                  Series
                </NavLink>
              </button>
              <button
                className="navigation__item"
                onClick={() =>
                  smallScreen.matches ? setCollapsed(!collapsed) : null
                }
              >
                <NavLink
                  to="/movies"
                  className="navigation__link"
                  activeClassName="navigation__link--active"
                >
                  Peliculas
                </NavLink>
              </button>
              <button
                className="navigation__item"
                onClick={() =>
                  smallScreen.matches ? setCollapsed(!collapsed) : null
                }
              >
                <NavLink
                  to="/recently-added"
                  className="navigation__link"
                  activeClassName="navigation__link--active"
                >
                  Añadidos recientemente
                </NavLink>
              </button>
              <button
                className="navigation__item"
                onClick={() =>
                  smallScreen.matches ? setCollapsed(!collapsed) : null
                }
              >
                <NavLink
                  to="/my-list"
                  className="navigation__link"
                  activeClassName="navigation__link--active"
                >
                  Mi lista
                </NavLink>
              </button>
            </div>
            <div className="navigation__menu__right">
              <SearchBox />

              <button className="navigation__item navigation__icon">
                <i className="fas fa-bell" />
              </button>
              <NavigationSettings />
            </div>
          </div>
        </Router>
      </div>
    </nav>
  );
};

export default Navigation;
